@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,500&display=swap);
* {
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0%;
  padding: 0%;
  max-width: 100vw;
  max-height: 100vh;
}

.app {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100vw;
  height: 100vh;
}

.app-content {
  width: 60%;
  display: flex;
}

.tasklist-form-and-items {
  width: 25%;
  padding: 16px;
  background-color: #212121;
}

.display-tasklist {
  display: flex;
  flex-direction: column;
  width: 55%;
  background-color: #f1f1f1;
}

.task-panel {
  width: 20%;
  padding: 16px;
  background-color: #fdfdfd;
  color: #ffffff;
}

.task-lists {
  width: 100%;
}

.task-list-item-container {
  display: flex;
  align-items: center;
  font-size: 18px;
}

.icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 25px;
  width: 30px;
  background-color: #b4b4b4;
  color: #000000;
  border-radius: 3px;
}

.task-list-item-container .icon {
  font-size: 24px;
  background-color: #b4b4b4;
  color: #ffffff;
}

.task-list-item-container input {
  display: flex;
  align-items: center;
  padding: 10px;
  width: 100%;
  font-size: 16px;
  color: #b4b4b4;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.task-list-item-container input:hover {
  transition: 250ms;
  color: #ffffff;
}

.task-list-item-container input::-webkit-input-placeholder {
  font-weight: 500;
  opacity: 1;
}

.task-list-item-container input:-ms-input-placeholder {
  font-weight: 500;
  opacity: 1;
}

.task-list-item-container input::placeholder {
  font-weight: 500;
  opacity: 1;
}

.task-list-item-container input:focus {
  outline: none;
}

.task-list-item {
    display: flex;
    flex-direction: row;
    font-size: 14px;
    padding: 10px 0px;
    cursor: pointer;
}

.task-list-item:hover {
    transition: 250ms;
    color: #FFFFFF;
}

.task-list-size {
    flex-grow: 1;
    display: flex;
    justify-content: right;
}
.task-list {
  display: flex;
  flex-direction: column;
  height: 100vh;
  margin: 12px;
}

.task-list-name {
  font-size: 24px;
}

.task-list-details {
  margin: 5px 0px;
  font-size: 16px;
  font-style: italic;
  color: rgb(141, 141, 141);
}

.task-list-list{
  flex-grow: 1;
  overflow: hidden;
  position: relative;
}

.task-list-list-content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
}

.tasklists-empty {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #686868;
}

.tasklists-empty .icon {
  font-size: 250px;
}

.tasklists-empty .text {
  font-size: 26px;
}
.task {
  min-height: 36px;
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 12px;
  margin-right: 5px;
}

.task-labels-container {
  margin-bottom: 10px;
  display: flex;
  flex-direction: row;
}

.task-content-container {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
}

.task .task-content {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.task-content .task-name {
  width: 95%;
  font-size: 16px;
  border: none;
  outline: none;
  background-color: transparent;
}

.task-content .task-completed {
  display: flex;
  justify-content: center;
  margin-right: 10px;
  font-size: 24px;
  width: 5%;
  cursor: pointer;
}

.task-completed .completed-false {
  color: #8d8d8d;
}

.task-completed .completed-true {
  color: #00c853;
}

.task-edit {
  display: flex;
  align-items: center;
  font-size: 18px;
  color: rgb(139, 139, 139);
  cursor: pointer;
}

.task-label {
  width: 100px;
  text-align: center;
  font-size: 12px;
  letter-spacing: 0.5px;
  color: #ffffff;
  padding: 2px 12px;
  border-radius: 100px;
}

.task-form {
    min-height: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 5px;
    margin: 5px 0px;
    padding: 0px 10px;
    background-color: #c2c2c2;
  }

  .task-form-container {
    display: flex;
    flex-direction: row;
    width: 100%;
  }
  
  .input-container {
    display: flex;
    flex-direction: row;
    border-radius: 8px;
    width: 100%;
  }
  
  .input-container input {
    width: 100%;
    padding: 0px;
    font-size: 16px;
    border: none;
    background-color: transparent;
    color: #000000;
  }
  
  .input-container input::-webkit-input-placeholder {
    color: #000000;
  }
  
  .input-container input:-ms-input-placeholder {
    color: #000000;
  }
  
  .input-container input::placeholder {
    color: #000000;
  }

  .input-container input:focus {
    outline: none;
  }
  
  .task-form-container .icon-container {
    display: flex;
    justify-content: center;
    margin-right: 10px;
    font-size: 24px;
    width: 5%;
    color: #000000;
    background-color: transparent;
  }
.label-title {
    color: rgb(0, 0, 0);
}

.task-labels {
  display: flex;
  flex-direction: column;
}

.task-labels .task-label-container {
  margin: 8px 0px;
  display: flex;
  flex-direction: row;
}

.task-label-radio {
  display: flex;
  align-items: center;
  color: #aaaaaa;
  margin-right: 14px;
  cursor: pointer;
}

